<template>
<example-main-attack :my-list="list">
  <template #attacker>
    <p class="font-bold text-white font-black w-full">مهاجمان تیم رئال مادرید</p>
  </template>
</example-main-attack>
</template>

<script>
import {onMounted, reactive} from "vue";
import axios from "axios";
import ExampleMainAttack from "@/components/main/ExampleMainAttack";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "MainAttack",
  components: {ExampleMainAttack},
  setup(){
    const list=reactive([])
    const sum=onMounted(async ()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/63'),
        axios.get('https://api.footballi.net/api/v2/player/16376'),
        axios.get('https://api.footballi.net/api/v2/player/2225721'),
        axios.get('https://api.footballi.net/api/v2/player/2258266'),
        axios.get('https://api.footballi.net/api/v2/player/2236700'),
      ])
          .then(axios.spread((...res)=>{
            list.push(res)
          }))
          .catch((error)=>{
         handleError(error)
          })

    })
    return{
      list,sum
    }
  }
}
</script>

