<template>
<example-transfer :my-list="menu">

</example-transfer>
</template>

<script>
import ExampleTransfer from "@/components/main/ExampleTransfer";
import {reactive,onMounted} from "vue";
import axios from "axios";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "TransferTeamReal",
  components: {ExampleTransfer},
  setup(){
    const menu=reactive([])
    const finish=onMounted(async()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/2236126'),
        axios.get('https://api.footballi.net/api/v2/player/2417159'),
        axios.get('https://api.footballi.net/api/v2/player/2317055'),
        axios.get('https://api.footballi.net/api/v2/player/2237242'),
        axios.get('https://api.footballi.net/api/v2/player/1517'),
        axios.get('https://api.footballi.net/api/v2/player/16377'),
        axios.get('https://api.footballi.net/api/v2/player/2272980'),
        axios.get('https://api.footballi.net/api/v2/player/3764'),
        axios.get('https://api.footballi.net/api/v2/player/264'),
        axios.get('https://api.footballi.net/api/v2/player/2276098'),
        axios.get('https://api.footballi.net/api/v2/player/2895'),
        axios.get('https://api.footballi.net/api/v2/player/2225301'),
        axios.get('https://api.footballi.net/api/v2/player/2257413'),
        axios.get('https://api.footballi.net/api/v2/player/2312118'),
        axios.get('https://api.footballi.net/api/v2/player/16377'),
      ])
          .then(axios.spread((...response)=>{
            menu.push(response)
          }))
          .catch((error)=>{
            handleError(error)
          })
    })

    return{
      menu,finish
    }
  }
}
</script>

