<template>
<example-defender :my-list="list">
  <template #defender>
    <p class="font-bold text-white font-black w-full">مدافعان تیم رئال مادرید</p>
  </template>
</example-defender>
</template>

<script>
import ExampleDefender from "@/components/main/ExampleDefender";
import {onMounted, reactive} from "vue";
import axios from "axios";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "DefenderReal",
  components: {ExampleDefender},
  setup(){
    const list=reactive([])
    const result=onMounted(async ()=> {
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/1517'),
        axios.get('https://api.footballi.net/api/v2/player/3035'),
        axios.get('https://api.footballi.net/api/v2/player/3185'),
        axios.get('https://api.footballi.net/api/v2/player/3681'),
        axios.get('https://api.footballi.net/api/v2/player/3779'),
        axios.get('https://api.footballi.net/api/v2/player/17708'),
        axios.get('https://api.footballi.net/api/v2/player/2257319'),
        axios.get('https://api.footballi.net/api/v2/player/2258796'),
        axios.get('https://api.footballi.net/api/v2/player/2287167'),
      ])
          .then(axios.spread((...response) => {
            list.push(response)
          }))
          .catch((error) => {
            handleError(error)
          })

    })
    return{
      list,result
    }
  }
}

</script>

