<template>
<example-attack :my-list="list">
  <template #Midfielder>
    <p class="font-bold text-white font-black w-full">هافبک های تیم رئال مادرید</p>
  </template>
  </example-attack>
</template>

<script>
import {onMounted, reactive} from "vue";
import axios from "axios";
import ExampleAttack from "@/components/main/ExampleAttack";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "AttackReal",
  components: {ExampleAttack},
  setup(){
    const list=reactive([]);
    const total=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/829'),
        axios.get('https://api.footballi.net/api/v2/player/3126'),
        axios.get('https://api.footballi.net/api/v2/player/15352'),
        axios.get('https://api.footballi.net/api/v2/player/42486'),
        axios.get('https://api.footballi.net/api/v2/player/2237242'),
        axios.get('https://api.footballi.net/api/v2/player/2272980'),
      ])
          .then(axios.spread((...res)=>{
            list.push(res)
          }))
          .catch((error)=>{
            handleError(error)
          })
    })
    return{
      list,total
    }
  }
}
</script>

