<template>
<section class="center-players">
  <div class="container mx-auto p-2">
    <div class="w-full h-auto">
      <p class="font-semibold text-start text-black w-full">بازیکنان تیم رئال مادرید</p>
      <div class="bg-black h-0.5 w-full my-1"></div>
      <div class="bg-green-500 w-full min-h-full relative px-3">
        <example-goal-keeper :my-list="result">
          <template #goalKeeper>
            <p class="font-bold text-white font-black w-full">دروازه بان های تیم رئال مادرید</p>
          </template>
        </example-goal-keeper>
        <defender-real></defender-real>
        <attack-real></attack-real>
        <main-attack></main-attack>
        <img :src="require('../../../assets/image/squad-middle.png')" alt="" class="h-auto absolute mx-auto bottom-2.5 w-full left-0 right-0">
      </div>
    </div>
  </div>
</section>
</template>

<script>
import ExampleGoalKeeper from "@/components/main/ExampleGoalKeeper";
import {onMounted, reactive} from "vue";
import axios from "axios";
import DefenderReal from "@/components/main/TeamReal/DefenderReal";
import AttackReal from "@/components/main/TeamReal/AttackReal";
import MainAttack from "@/components/main/TeamReal/MainAttack";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "PlayersReal",
  components: {MainAttack, AttackReal, DefenderReal, ExampleGoalKeeper},
  setup(){
    const result=reactive([]);
    const total=onMounted(async ()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/262'),
        axios.get('https://api.footballi.net/api/v2/player/2224349'),
      ])
          .then(axios.spread((...response)=>{
            result.push(response)
          }))
          .catch((error)=>{
           handleError(error)
          })

    })
    return{
      result,total
    }
  }
}
</script>

